var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"article"},[_c('p',[_vm._v("有一陣子，張振每天都花四、五小時瀏覽各種色情平台，為的是尋找被洩漏的女友照片。")]),_c('p',[_vm._v("2021年夏天，一個朋友告知張振，在一個色情app上看到了他女友的私密照片。得知照片洩露後，張振和女友感到著急又害怕。他私信app的客服要求刪除照片，客服要求他提供身份證驗明身份，最後刪除了照片。不過張振發現，女友的照片已經在不同平台、聊天群組中被大量轉發，人們用不堪的字句來討論、評價這個女生。")]),_c('p',[_vm._v("這些照片是張振與女友私下拍攝的。他曾經通過QQ發照片給女友，聊天記錄中因此保留了這些照片，大約有幾十張。數月前，他的QQ被盜號，但當天便找回，當時他沒有察覺異常。他懷疑正是盜號使得照片洩露了。")]),_c('p',[_vm._v("到底照片被散播到哪些地方？散播的源頭是哪裡？怎麼可以徹底刪除這些照片？在隨後半年，張振獨自在互聯網中大海撈針，他下載了16款色情app，同時「翻牆」瀏覽Twitter，又潛入各種Telegram色情群組中……")]),_c('h3',[_vm._v("付費私密群、色情「資源」：多種方式避免直接交易")]),_c('p',[_vm._v("張振最終在10款色情app和7個色情網站上發現了女友被洩露照片。「而且不是只有一個人發，一個軟件裡面有10多個人發了我們的照片，」 張振很想知道，這些人是從哪兒得到自己的照片。")]),_c('p',[_vm._v("查找只有笨辦法，張振逐個平台翻查。他最早是在色情app上發現女友洩露的照片。這些app中，「91」、「草榴」是由色情網站開發，他順著又找到許多色情網站。色情網站中有的帖又會註明，其照片、影片來自Twitter或Telegram私密群。")]),_c('p',[_vm._v("他發現，這些色情app和色情網站都不需要「翻牆」，但Telegram、Twitter就被中國大陸封鎖，需要「翻牆」才能使用。在此之前，張振從來沒有「翻牆」過，他拜託在國外的同學給了他一個VPN，註冊了Twitter。")]),_c('p',[_vm._v("「Twitter有很多這些app和網站用來做推廣，專門在Twitter上發他們的相關資源，簡介上都會掛上網站或者app連結或者是電報群連結，通過他們的賬號簡介點擊這些連結，直接就可以找到其他的。」張振說。")]),_c('p',[_vm._v("色情網站、Twitter賬號往往會為女生的照片配文，描述女生是「校花」、「少婦」、「網貸下海」以及性愛動作，在Google上檢索這些文字，便能找到大量其他色情平台也發佈了同樣內容。")]),_c('div',{staticClass:"img"},[_c('img',{attrs:{"src":require("./../../assets/imgs/news-2-1.png")}}),_c('p',[_vm._v("一個色情群組中的聊天内容")])]),_c('p',[_vm._v("Telegram則藏了更龐大的色情內容交流群組或頻道。張振發現，不少群組成員眾多，最多人的群組有20萬人，這也是Telegram群組的人數上限。群組中有人發佈女性的生活照，配以淫穢話語，也有人發佈性愛照片或影片；有人提供照片中人的聯繫方式，希望其他群友去騷擾。更多群友只是默默發出不知來歷的照片，既有生活照，也有曝露私處的照片。")]),_c('p',[_vm._v("這些色情群組不少主打「反差」，即群組成員發出女性的日常生活照，意淫羞辱其如何「淫蕩」，群組中最常用來指代女性的詞是「母狗」。群組中也鼓勵「盯射」，一些群組成員將精液射到顯示女性照片的屏幕上，並將男性生殖器與女生照片一起「合照」。")]),_c('p',[_vm._v("據端傳媒記者觀察，這類Telegram群組大致有兩種盈利方式：一是發賭博遊戲、色情軟件的廣告；另一種是售賣付費私密群，承諾私密群中有更多「資源」，即打包好的色情照片、影片。在這些群組中付款，普遍使用支付寶口令紅包，即付款者發一個口令紅包，收款者僅需在支付寶輸入口令便可領取。還有的群組會要求使用虛擬貨幣支付，對於不會使用虛擬貨幣的，群組提供了購買虛擬貨幣的教程，下載一款指定的交易所app，按照app提示購買就可以。")]),_c('p',[_vm._v("記者支付了14個泰達幣（折合95.55元人民幣）之後，進入了一個私密頻道，資源主打「蘿莉、幼女」，照片大多為自拍視角，主角明顯是小學女生或初中女生；其中也有成人的照片，有的成人照片上附有微博賬號水印，記者向這些微博賬號查詢，其中一個賬號回覆，這是她很久以前的照片。")]),_c('p',[_vm._v("該頻道管理員持續不斷地更新群內資源。記者發稿前，群內出現了名為「韓國N號房」的文件夾，其中出現讓人不安的兒童色情視頻，視頻中的兒童用韓語在自己的身體寫上「母狗」、「前輩奴隸」等字樣，兒童說的也是韓語，她們的年齡看起來有的是小學生，有的是初中生。")]),_c('p',[_vm._v("發佈「反差」內容的Twitter賬號常常在主頁附上其Telegram群組的入口。Twitter賬號發佈、轉發色情照片，將用戶引流到Telegram群組及色情網站，再在付費私密群和色情網站上消費，如此形成了一條遍布不同平台的色情產業鏈。")]),_c('div',{staticClass:"img"},[_c('img',{attrs:{"src":require("./../../assets/imgs/news-2-2.jpg")}}),_c('p',[_vm._v("涉及兒童色情内容的色情群組，其中有多個文件夾名爲「韓國N號房」")])]),_c('p',[_vm._v("中文色情網站大多是論壇形式，絕大多數在中國大陸網絡中也可以打開，其盈利方式主要是會員充值站內「貨幣」，用於出售或購買色情「資源」。")]),_c('p',[_vm._v("記者發現，其中一個色情網站「私房俱樂部」使用了自動發卡收款平台，點擊充值會跳轉到發卡平台，記者多次掃描發卡平台提供的支付寶收款碼，分別是給某個+86開頭的電話號碼充值話費、給某個支付寶個人賬戶轉賬、給一家桂林的網絡科技公司轉賬，相當於是一種洗錢手段，實際收款方借此規避支付寶的風險控制。")]),_c('p',[_vm._v("與Telegram私密群使用支付寶口令紅包、虛擬貨幣支付相同，這三種付款方式都避免了直接交易。")]),_c('div',{staticClass:"img"},[_c('img',{attrs:{"src":require("./../../assets/imgs/news-2-3.jpg")}}),_c('p',[_vm._v("「私房俱樂部」使用的自動發卡收款平台，網站借此避免直接交易")])]),_c('h3',[_vm._v("受害者：付費才能「買斷資源」")]),_c('p',[_vm._v("Twitter上有大量「反差」、「盯射」主題的黃推，即發佈色情內容的Twitter賬號，張振在多個Twitter賬號上找到了女友照片。他逐一私信這些賬號，表明自己是受害者，希望對方刪除圖片。")]),_c('p',[_vm._v("其中一個賬號同意刪除圖片，還告訴張振，照片來自色情網站「草榴社區」。這是早於2006年成立、規模龐大的一個中文色情論壇。2019年，黑龍江公安曾抓獲其4名「核心管理成員」，這4人成立了一個發佈原創色情照片的分享團，「該分享團建立了十個全國群，群內「遊客」2萬多人，核心會員2300多人，通過分享團發出的原創色情網帖超過3000個，圖片五萬餘張，跟帖回覆超過50萬，瀏覽量超千萬。」")]),_c('p',[_vm._v("但草榴目前仍在正常運作，還開發了自己的app。實際上，這類色情網站的服務器大多架設在中國境外，中國互聯網防火牆實行白名單制度，屏蔽色情網站的一個網址，站方可以快速更換為還未被屏蔽的網址。")]),_c('p',[_vm._v("不過，草榴社區並不是照片的唯一源頭，張振先後在7個色情網站上找到了女友被洩漏的照片。他通過站內郵件私信了其中2個網站的管理員，一個網站他先是私信了發佈者，發佈者表示管理員才有權限刪帖，隨後管理員已讀不回，默默刪除了照片。")]),_c('p',[_vm._v("另一個網站的大版主已讀不回，沒有任何動作。張振又聯繫了小版主，小版主刪除了照片。「他跟我說我們是成人網站，我也是幫你義務刪除，你不要聯繫大版主，他知道的話會撤銷掉我的管理員身份，還會把我刪除的帖子恢復。」張振表示。")]),_c('p',[_vm._v("小版主同時告訴張振，他們也是從別處轉載的照片，並指點他根據照片上的水印去找來源。")]),_c('p',[_vm._v("張振在查找中發現，在多家網站上，他女友照片上都有一個寫著「sifang」（即「私房」的漢語拼音）的水印，張振最終瞄準了色情網站「私房俱樂部」，要求對方刪除。網站要求張振拍一張生活照來驗證身份，並且要求他付費「買斷」資源，才會刪除照片。")]),_c('p',[_vm._v("「私房俱樂部」分免費版塊和付費版塊，並設有自己的「私房幣」，1.1元人民幣充值1私房幣。在付費版塊中，色情「資源」通常要價以3至50私房幣。")]),_c('p',[_vm._v("網站中所有版塊都需要註冊網站會員才能查看圖片及下載資源，內容為偷拍的性愛視頻，不少發佈者聲稱是原創。另外，會員需要累計充值2000私房幣（折合2200元人民幣）、註冊會員3個月以上、同時私信管理員審核，才能購買到進入網站VIP版塊的資格。")]),_c('p',[_vm._v("端傳媒記者在多家色情網站中翻查發現，其中的「資源」大多是會員從各處搬運而來，照片上的水印也包括了多家網站。帖子中自稱的「原創內容」，也無法確定是否原創，有可能是從Telegram群組等處搬運，其中可能涉及被盜取、偷拍照片的女性受害者。")]),_c('p',[_vm._v("張振說，他多次向Telegram群組內的人請求刪除女友照片時，有人不願意刪除，有人要求他和女友持續提供私密照片供他「意淫」，也有人向他勒索800元人民幣，並表示自己不怕警察。")]),_c('p',[_vm._v("後來，他不再自稱「男友」，因為他發現那樣反而會讓對方更興奮，或者揶揄嘲笑自己，轉而以長輩或受害者家屬的身份去要求刪照片。")]),_c('p',[_vm._v("張振表示，自己私下問過一個網絡警察，有人以女友私密照向他敲詐勒索，應該怎麼辦。「他跟我說，建議我不去理會，因為沒什麼好的辦法解決。」張振稱，對方還告訴他，中國各個城市的網監部門每年都會遇到很多涉案金額高達百萬、千萬的電信詐騙案，「這些警力都不夠，所以很難有精力分擔出來去處理你（我）們這樣的事情。」")]),_c('p',[_vm._v("「其實說白了，就是得不到有關部門的重視。」張振說。")]),_c('div',{staticClass:"img"},[_c('img',{attrs:{"src":require("./../../assets/imgs/news-2-4.png")}}),_c('p',[_vm._v("Twitter賬號「收藏家」新賬號的主頁，采訪當中，他有9000多名跟隨者的大號被永久封禁")])]),_c('h3',[_vm._v("刪不盡的照片")]),_c('p',[_vm._v("Twitter上有一個名為「收藏家」的賬號，發佈了大量女性的裸體照片，目前有9000多名跟隨者。")]),_c('p',[_vm._v("這個賬號以文字信息的方式接受了端傳媒記者的採訪。他對記者稱，他收藏了「幾百個人」的「反差照片」，照片的來源一半是照片本人投稿，一半是他在Telegram色情群組中看到並保存的。")]),_c('p',[_vm._v("「我個人收藏，除了本人同意分享和曝光的，其他我都沒發，和他們牟利的有根本區別，」收藏家自稱沒有利用照片來謀利。同時他稱，對於投稿的，他會要求對方證明身份，包括要求投稿者在身體寫上身份證號。")]),_c('p',[_vm._v("但他也表示，他只能驗證自己接收的投稿，從Telegram群組中保存再發佈的，無法驗證是否本人自願拍攝的。")]),_c('p',[_vm._v("會否有女性被脅迫、要求在身體上寫上身份證號？收藏家稱，「這個可能性很低了，現在能脅迫人的很少，女性地位已經起來了」、「這樣的話，我們發出去還能幫助女方身邊人發現，還能讓她早點脫離。」")]),_c('p',[_vm._v("這個賬號表示，過去不少受害者聯絡他，希望他刪除照片，他都會同意。他稱尋求幫助的一般是男方，「一般需要幫助的都是情侶之間情趣，後面手機壞了，或者網盤洩漏，被人發了出來。」他不願透露至今有多少受害者聯絡過他。")]),_c('p',[_vm._v("現在，張振已經不再每天花數小時尋找被洩漏的女友照片，但他還是會兩三天搜查一次，每次基本花一個多小時查看色情平台。記者發稿前，張振又在一款色情app上找到了女友的照片，並且照片是新近發佈的。")]),_c('p',[_vm._v("2021年夏天發現洩漏至今，張振已經記不清成功刪除了多少照片，也不知道還有多少沒有被刪除。")]),_c('p',[_vm._v("香港關注婦女性暴力協會從去年2月1日開始推出「Ta-DA下架支援」項目，協助被散播私密影像的受害者與網絡平台溝通，以移除照片。協會倡議主任簡敏棋向端傳媒表示，截止2021年底，項目共收到238條包含未經同意私密影像的網絡連結，經溝通後，179個連結被移除。她指出，協會常收到涉及Telegram的求助，但Telegram一般不會回應移除內容的請求。")]),_c('p',[_vm._v("張振自己總結，一定不要聯繫個人，盡量聯繫網站管理員、Telegram群主、Twitter官方。那名勒索更多照片的人，張振告知了所在群主，表示自己已經報警，群主刪除了照片，剔除了勒索者，並要求張振也退群。")]),_c('p',[_vm._v("他分析，「一些電報群好幾萬人，即便你去報警的話，不可能把好幾萬人都抓住，但是創建者不一樣，他們自我保護意識也非常強，他們也不想去惹這個麻煩。」")]),_c('p',[_vm._v("張振發現，Twitter上的洩露照片最好處理。他在Twitter投訴了4、5次，Twitter官方會發來郵件要求提交護照或者駕照照片證明是本人，一般一天之內就會刪除照片。曾經有一個賬號，張振舉報過一次，Twitter官方刪除了照片。這個賬號又發，張振再次舉報，Twitter官方封禁了賬號。")]),_c('p',[_vm._v("端傳媒發稿前，「收藏家」原先的賬號已經被Twitter封禁，但很快又啓用了新賬號。")]),_c('p',[_vm._v("（文中張振為化名）")]),_c('p',[_vm._v("（端傳媒實習記者陸眉間、胡士托對本文亦有重要貢獻）")])])}]

export { render, staticRenderFns }